@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@400;500;600&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: Mulish;
}


.navbar {
  background-color: rgb(60, 164, 233) !important;

  box-shadow: 0 5px 10px 0 rgb(88, 79, 79);
}
.navbar-brand {
  font-size: 1.9rem !important;
  color: #565387 !important;
}
.navbar a {
  font-size: 1.3rem;
  text-transform: capitalize;
  font-weight: bold;
  
}

.nav-link:hover {
  color: #150daa !important;
}

@media (max-width: 991px) {
  .navbar-brand {
    font-size: 1.9rem !important;
    margin-left: 25px;
  }

  .nav-item {
    
    text-align: center;
    
  }

  
}

.poster {
  background-image: url(/portfolioWeb/static/media/back.6816c4b3.jpg);
  height: 78vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.div70 {
  width: 70%;
  float: left;
}
.div70 h1 {
  
  margin-top: 10px;
  font-size: 40px;
  font-weight: bold;
}

.div30 {
  width: 30%;
  float: left;
}

.div30 img {
  width: 170px;
  border: 1px solid white;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
}

#uni {
  padding: 100px 0;
}

.btns{
  transition: 0.3s ease-in-out;
  background-color: rgb(129, 80, 245);
  font-size:15px;
  color: white;
  font-weight: bold;
}

.btns:hover{
  box-shadow: inset 120px 0 0 0 yellow;
  outline:none;
}

@media (max-width: 991px) {
  .div30,
  .div70 {
    width: 100%;
    align-items: center;
    text-align: center;
  }
  .div30 img {
    width: 250px;
    margin-top: 80px;
    text-align: center;
  }

  #uni {
    padding: 30px 0;
  }
}
@media (max-width: 500px) {
  .div30 img {
    width: 250px;
    margin-top: 60px;
  }
  #uni {
    padding: 0;
  }
}
@media (max-width: 300px) {
  .div30 img {
    width: 100px;
    margin-top: 20px;
  }
  #uni {
    padding: 0;
  }
}

.about{
    align-items: center;
    text-align: center;
    padding-top: 25px;
    
  
}

.row h2{
    font-size: 30px;
    font-weight: bold;
    text-decoration: underline;
    text-decoration-thickness: 5px;
    text-underline-offset: 8px;
    
    
}

.header-img img{
    border: 1px solid rgb(85, 59, 11);
    box-shadow: 10px 10px  rgb(79, 44, 235),-10px -10px  rgb(99, 98, 96);
}

@media(max-width:901px){
    .header-img img{
        width: 100%;
        align-items: center;
        text-align: center;
        justify-content: center;
        margin-top: 30px;
      }
}




.skill{
    margin-top: 30px;
    text-align: center;
    font-weight: bold;
    
}

.skill h2{
    font-size: 30px;
    font-weight: bold;
    text-decoration: underline;
    text-decoration-thickness: 5px;
    text-underline-offset: 8px;
    padding-bottom: 20px;
}

.div40{
    width: 40%;
    float: left;
    padding-top: 25px;
}

.div40 p{
    align-items: center;
    justify-content: center;
    
}

.div40 h3{
    font-weight: bold;
    font-size: 25px;
}

.div60{
    width: 60%;
    float: left;
   
    
}
.div60 h3{
    font-size: 20px;
}

@media(max-width: 991px){
    .div60, .div40{
        width: 100%;
        align-items: center;
        justify-content: center;
        text-align: center;
    }
}


.resize{
    resize: none;
}

.cont{
    font-size: 45px;
    font-weight: bold;
    text-decoration: underline;
    text-underline-offset: 5px;
    text-decoration-thickness: 5px;
}
.card{
    width: 350px;
    height: 250px;

    box-shadow: 5px 5px 1px rgb(78, 50, 50);
    
}

.card-title{
    font-weight: bold;
    font-size: 30px;
    text-align: center;
}

.pro h3{
    font-size: 30px;
    font-weight: bold;
    text-decoration: underline;
    text-decoration-thickness: 5px;
    text-underline-offset: 8px;
   
}
.card{
    transition: 0.5s all ease-in-out;
    background-color: yellowgreen;
}

.card:hover{
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
    
}

@media(max-width:991px){
    .card{
        width: 100%;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 150px;
        margin-top: 50px;
        
    }

    .card-title{
        font-size: 15px;
        font-weight: bold;
        
    }

    .card-text{
        font-size: 10px;
    }
}
